<script>
import { useAppOptionStore } from '@/apps/landing/stores/app-option';
import VueCountdown from '@chenfengyuan/vue-countdown';

const appOption = useAppOptionStore();

export default {
	data() {
		const now = new Date();
		const newYear = new Date(2025, 6, 26);

		return {
			year: (new Date()).getFullYear(),
			time: newYear - now,
		}
	},
	components: {
		VueCountdown: VueCountdown
	},
	mounted() {
		appOption.appHeaderHide = true;
		appOption.appFooter = false;
		appOption.appContentClass = 'p-0';
	},
	beforeUnmount() {
		appOption.appHeaderHide = false;
		appOption.appFooter = true;
		appOption.appContentClass = '';
	}
}
</script>
<template>
	<!-- BEGIN coming-soon -->
	<div class="coming-soon">
		<div class="flex-1">
			<!-- BEGIN coming-soon-content -->
			<div class="coming-soon-content">
				<div class="coming-soon-img mb-4"><img src="/assets/img/page/coming-soon.svg" alt="" /></div>
				<div class="coming-soon-timer mb-3">
					<div class="is-countdown text-center">
						<vue-countdown class="countdown-row countdown-show4" :time="time"
							v-slot="{ days, hours, minutes, seconds }">
							<span class="countdown-section">
								<span class="countdown-amount d-block">{{ days }}</span>
								<span class="countdown-period d-block">{{ $t("APP.ERRORS.MAINTENANCE.COUNTDOWN.DAYS") }}</span>
							</span>
							<span class="countdown-section">
								<span class="countdown-amount d-block">{{ hours }}</span>
								<span class="countdown-period d-block">{{ $t("APP.ERRORS.MAINTENANCE.COUNTDOWN.HOURS") }}</span>
