							<span class="countdown-section">
								<span class="countdown-amount d-block">{{ minutes }}</span>
								<span class="countdown-period d-block">{{ $t("APP.ERRORS.MAINTENANCE.COUNTDOWN.MINUTES") }}</span>
							</span>
							<span class="countdown-section">
								<span class="countdown-amount d-block">{{ seconds }}</span>
								<span class="countdown-period d-block">{{ $t("APP.ERRORS.MAINTENANCE.COUNTDOWN.SECONDS") }}</span>
							</span>
						</vue-countdown>
					</div>
				</div>
				<h2 class="mb-1">{{ $t("APP.ERRORS.COMINGSOON.TITLE") }}</h2>
				<p class="mb-0">{{ $t("APP.ERRORS.COMINGSOON.DESC.1") }}<br /> {{ $t("APP.ERRORS.COMINGSOON.DESC.2") }}
